*, *:before, *:after {
    box-sizing: border-box;
}

html, body {
    margin: 0;
    padding: 0;
}

*, a, input, select, textarea, button {
    font-family: 'Lato', sans-serif;
}

*:not(input) {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-user-drag: none;
    -khtml-user-drag: none;
    -moz-user-drag: none;
    -ms-user-drag: none;
    user-drag: none;
}

.btn {
    display: block;
    padding: 13px 0 14px;
    background: #dc0000;
    border: none;
    box-shadow: 0 4px 12px rgba(220, 0, 0, 0.1);
    border-radius: 64px;
    width: 100%;
    font-family: 'Lato', sans-serif;
    font-style: normal;
    font-weight: 900;
    font-size: 13px;
    line-height: 1;
    text-align: center;
    text-transform: uppercase;
    color: #ffffff;
    cursor: pointer;
    outline: none;
    transition: background 0.2s;

    &:hover, &:focus, &:active {
        background: #9e0101;
    }

    @media (min-width: 2560px) {
        font-size: 17px;
        padding: 17px 0 16px;
    }

    @media (min-width: 3840px) {
        font-size: 26px;
        padding: 22px 0 28px;
        border-radius: 128px;
    }
}

.perfect {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    pointer-events: none;
    z-index: 100000;

    img {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0.5;
        width: 100vw;
        height: auto;
    }
}

