.sign_in_forgot_password_modal {
    width: 415px;
    background: #ffffff;
    box-shadow: 0px 4px 14px rgba(0, 0, 0, 0.04);
    border-radius: 22px;
    padding: 30px 30px 50px;

    .forgot_password_form {
        form {
            [data-placeholder][data-label] {
                position: relative;

                &:before,
                &:after {
                    transition: opacity 0.15s;
                    display: block;
                    position: absolute;
                    left: 56px;
                    pointer-events: none;
                    opacity: 0;
                    font-family: 'Lato', sans-serif;
                    font-style: normal;
                    font-weight: 700;
                    line-height: 1;
                    letter-spacing: 0.5px;
                }

                &:before {
                    content: attr(data-placeholder);
                    top: 33px;
                    font-size: 15px;
                    color: rgba(1, 1, 1, 0.4);
                }

                &:after {
                    content: attr(data-label);
                    top: 20px;
                    font-size: 13px;
                    color: rgba(1, 1, 1, 0.6);
                }

                &:not(.filled):before {
                    opacity: 1;
                }

                &.filled:after {
                    opacity: 1;
                }
            }

            .forma {
                .form-group {
                    width: 340px;
                    position: relative;
                    margin: 0 auto;

                    label {
                        display: block;
                        position: absolute;
                        top: 31px;
                        left: 23px;
                    }

                    input {
                        display: block;
                        background: #efefef;
                        border-radius: 8px;
                        border: none;
                        outline: none;
                        padding: 41px 10px 20px 58px;
                        font-size: 17px;
                        line-height: 1;
                        font-weight: 900;
                        width: 100%;
                    }
                }

                .instruction {
                    max-width: 256px;
                    margin: 36px auto 49px;
                    font-family: 'Lato', sans-serif;
                    font-style: normal;
                    font-weight: normal;
                    font-size: 12px;
                    line-height: 1.2;
                    text-align: center;
                    color: #000000;
                    opacity: 0.5;
                }
            }

            .btn {
                &.disabled {
                    opacity: 0.12;
                    pointer-events: none;
                }
            }
        }
    }
}

