                                background-color: #dc0000;
                                border-radius: 50%;
                                background-image: url("../img/done.svg");
                                background-repeat: no-repeat;
                                background-position: center;
                            }
                        }

                        .start_content{
                            position: relative;
                            width: 336px;
                            height: 48px;
                            padding: 0 53px 0 22px;
                            margin-bottom: 9px;
                            background: #EFEFEF;
                            border-radius: 8px;
                            display: flex;
                            flex-direction: row;
                            justify-content: flex-start;
                            align-items: center;

                            span{
                                font-style: normal;
