                                        border-right: 6px solid #000000;
                                    }
                                }
                            }
                        }
                    }
                }

                .btn {
                    padding: 15px 0 14px;

                    &.disabled {
                        opacity: 0.12;
                        pointer-events: none;
                    }
                }
            }
        }
    }
}

