body .remodal-wrapper {
    .remodal {
        .title {
            margin: 0 0 40px;
            font-style: normal;
            font-weight: 900;
            font-size: 21px;
            letter-spacing: 0.4px;
            line-height: 1.2;
            text-align: center;
            color: #000000;
        }

        .remodal-content {
            transition: opacity 0.2s;
        }

        .remodal-close {
            width: 72px;
            height: 84px;
            left: unset;
            left: auto;
            right: 0;
            opacity: 1;
            background-image: url("../img/close.svg");
            background-repeat: no-repeat;
            background-position: center;

            &:before {
                content: none;
                display: none;
            }
        }
    }
}

body .remodal-wrapper {
    .remodal.sign_in_computer_locked_modal {
        .remodal-close {
            background-image: url("../img/closewhite.svg");
            background-repeat: no-repeat;
            background-position: center;
        }
    }
}

body .remodal-wrapper {
