}

body.remodal-overlay-without-animation .remodal-overlay {
    &, &.remodal-is-opening, .remodal-is-closing {
        -webkit-animation-name: none !important;
        animation-name: none !important;
        opacity: 1 !important;
    }
}

body.remodal-content-loading .remodal {
    &:before {
        content: '';
        display: block;
        position: absolute;
        left: 16px;
        top: 20px;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        border: 4px solid #dc0000;
        border-right-color: transparent;
        -webkit-animation-timing-function: linear;
        animation-timing-function: linear;
        -webkit-animation-iteration-count: infinite;
        animation-iteration-count: infinite;
        -webkit-animation-duration: 0.5s;
        animation-duration: 0.5s;
        -webkit-animation-name: remodal-loading-rotate;
        animation-name: remodal-loading-rotate;
    }

    .remodal-content {
        opacity: 0.5;

        &, * {
            pointer-events: none;
        }
    }
}

@-webkit-keyframes remodal-loading-rotate {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

@keyframes remodal-loading-rotate {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}